
body {
  margin: 0;
  padding: 0;
  font-family: Inter,Helvetica Neue,Helvetica,Arial,sans-serif;  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: grey;
  font-size: 14px;
  line-height: 2;
}

div {
  box-sizing: border-box;
}

h1 {
  font-weight: 100;
}
h1,
h2,
h3,
p {
  margin: 0;
}

h1 {
  color: rgb(52 52 96);
  font-weight:700;
}

ul {
  padding-left: 15px;
  text-align: justify;
}

hr {
  width: 100%;
  margin: 0;
}

#head {
  background-color: rgb(52 52 96);
  color: white;
  font-size: 16px;
  font-weight: 600;
  padding: 0 15px;
  border-radius: 3px;
  margin: 20px 0;
}

#head > h1 {
  margin: 0;
  color: white;
  font-size: 2.5rem;
  font-weight: 600;
}

.edit-title {
  background-color: #d8d8d8;
  padding: 10px;
  margin-top: 50px;
}

label {
  display: block;
}

input,
textarea {
  font-family: inherit;
}

textarea {
  width: 96%;
  margin-bottom: 20px;
}

.inputs-container {
  display: flex;
  flex-direction: column;
  grid-gap: 15px;
  gap: 15px;
  font-size: inherit;
  font-weight: 700;
}

/* .inputBox {
  display: grid;
  justify-items: start;
} */

.layout {
  display: grid;
  justify-items: start;
  width: 100%;
}

.item{
  display: grid;
  grid-template-columns: 100px auto auto;
  justify-items: start;
  align-items: center;
  margin-top: 5px;
  width: 100%;
  padding: 10px;
  grid-gap: 50px;
  gap: 50px;
}

.dates {
  font-weight: bold;
  align-self: flex-start;
}

form{
  margin: 0 auto;
  background-color: aliceblue;
  padding: 10px;
}

.button-bar {
  display: flex;
  justify-content: flex-end;
  width: 100%;
  align-self: flex-end;
}

button {
  background-color: rgb(94, 94, 220);
  border: none;
  margin: 5px 5px 5px 0;
  padding: 10px;
  width: 100px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  color: white;
  border-radius: 5px;
}

.delete-btn {
  background-color: rgb(241, 84, 84);
}

button:hover {
  cursor: pointer;
}

hr {
  margin-bottom: 15px;
}

.App {
  max-width: 900px;
  min-width: 320px;
  margin: 0 auto;
  margin-top: 20px;
  padding: 10px 60px 60px 30px;
  background-color: white;
  border-radius: 3px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.heart {
  color: #ff0000;
}

.small {
  font-size: 0.75rem;
}
